<template>
  <div class="Registered">
    <el-dialog
      :visible.sync="isDialogShow"
      custom-class="registeredDialog"
      width="400px"
      @close = "closeRegistered"
      append-to-body>
      <div class="registeredContainer">
        <div class="logo">
          <img src="../../assets/img/registered.png" alt="" />
        </div>
        <!--  -->
        <el-form class="form" :model="userInfo" v-if="currentStep == 0">
        <el-form-item>
          <el-input
            class="mobile"
            prefix-icon="el-icon-mobile-phone"
            size="small"
            placeholder=""
            v-model="userInfo.mobile"
          ></el-input></el-form-item
        ><el-form-item>
          <el-input
            class="password"
            prefix-icon="el-icon-lock"
            size="small"
            placeholder=""
            type="password"
            v-model="userInfo.password"
          ></el-input>
          <div class="tip">
            8~20//2
          </div></el-form-item
        >
        <el-form-item>
          <!---->
          <el-button
            @click="submitCount"
            size="small"
            :loading="isButtonLoading"
            ></el-button
          >
        </el-form-item>
        </el-form>
        <!--  -->
        <el-form class="form" v-else-if="currentStep == 1">
          <el-input
            class="mobile"
            prefix-icon="el-icon-key"
            size="small"
            placeholder=""
            v-model="code"
          ></el-input>
          <div class="codeButtonContainer">
            <div class="codeButton" v-if="!isCountDownShow" @click="getCode">
              
            </div>
            <div class="countDown" v-else>{{ countDown }}<span>s</span></div>
          </div>
          <el-form-item>
          <el-button @click="submitCode" size="small" :loading="isButtonLoading"
            ></el-button
          >
          </el-form-item>
        </el-form>
        <!-- --to do  -->
        <el-form class="form" v-else-if="currentStep == 2">

          <el-form-item>
            <el-input
            class="mobile"
            prefix-icon="el-icon-user"
            size="small"
            placeholder=""
            v-model="userInfo.nickname">
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-button
              @click="finishRegistered"
              size="small"
              :loading="isButtonLoading"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>

    </el-dialog>

  </div>
</template>

<script>
// 
let timer

export default {
  props: {
    isDialogShow: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  data () {
    // 
    // 
    return {
      // 
      currentStep: 0,
      // 
      userInfo: {
        mobile: '',
        password: '',
        nickname: ''
      },
      // 
      code: '',
      // 
      isButtonLoading: false,
      // 
      countDown: 60,
      // 
      isCountDownShow: false
    }
  },
  methods: {
    // 
    async getCode () {
      // console.log('')
      const getCode = await this.$request('/captcha/verify', {
        phone: this.userInfo.mobile
      })
      console.log(getCode)
      if (getCode.data.code === 200) {
        //   
        this.countDown = 60
        this.isCountDownShow = true
        timer = setInterval(() => {
          if (this.countDown === 0) {
            clearInterval(timer)
            this.isCountDownShow = false
            return
          }
          this.countDown--
        }, 1000)
      } else {
        this.$message.error(getCode.data.message)
      }
    },
    // 
    async submitCount () {
      console.log('')
      // 
      this.isButtonLoading = true
      // 
      if (this.userInfo.mobile.length !== 11 || this.userInfo.password.length < 8) {
        this.$message.warning('!')
        this.isButtonLoading = false
        return 0
      }
      // 
      const verifyMobile = await this.$request('/cellphone/existence/check', {
        phone: this.userInfo.mobile
      })
      console.log(verifyMobile)
      if (verifyMobile.data.exist !== 1) {
        // 
        await this.getCode()
        // 
        this.isButtonLoading = false
        this.currentStep = 1
      } else {
        // 
        this.isButtonLoading = false
        this.$message.warning(',!')
        // 
        this.closeRegistered()
      }
    },
    // 
    async submitCode () {
      console.log('')
      // 
      this.isButtonLoading = true
      // 
      if (this.code.length !== 4) {
        this.isButtonLoading = false
        this.$message.warning('!')
        return
      }
      const verifyCode = await this.$request('/captcha/verify', {
        phone: this.userInfo.mobile,
        captcha: this.code
      }).catch((err) => {
        this.$message.error(err.response.data.message)

        // 
        this.isButtonLoading = false
      })
      if (!verifyCode) {
        return false
      }
      // 
      this.isButtonLoading = false
      console.log(verifyCode)
      if (verifyCode.data.data) {
        // 
        //    
        this.currentStep = 2
      }
    },
    //  -- to do

    // 
    async finishRegistered () {
      console.log('')
      // 
      this.isButtonLoading = true
      // 
      const res = await this.$request('/register/cellphone', {
        phone: this.userInfo.mobile,
        password: this.userInfo.password,
        nickname: this.userInfo.nickname,
        captcha: this.code
      }).catch((err) => {
        this.$message.error(err.response.data.message)
        // 
        this.isButtonLoading = false
      })
      if (!res) return false
      // 
      // 
      this.isButtonLoading = false
      console.log(res)
      if (res.data.code === 200) {
        this.$message.success('')
        this.$emmit('closeRegistered')
      } else {
        this.$message.error('!')
      }
    },
    // 
    closeRegistered () {
      // 
      this.userInfo = {
        mobile: '',
        password: '',
        nickname: ''
      }
      this.currentStep = 0
      this.code = ''
      this.isButtonLoading = false
      this.isCountDownShow = false
      this.countDown = 60

      this.$emit('closeRegistered')
    }
  }
}
</script>

